	<view class="diary-container">
		<view class="content">
			<!-- ÂåøÂêçÊòüÁêÉÂÖ•Âè£ -->
			<view class="anonymous-entry" @tap="goToAnonymous">
				<view class="entry-content">
					<view class="entry-icon">üåç</view>
					<view class="entry-info">
						<text class="entry-title">ÂåøÂêçÊòüÁêÉ</text>
						<text class="entry-desc">ÂàÜ‰∫´‰Ω†ÁöÑÂøÉÊÉÖÔºåÈÅáËßÅÊúâË∂£ÁöÑÁÅµÈ≠Ç</text>
					</view>
					<view class="entry-arrow">‚Ä∫</view>
				</view>
			</view>
			
			<!-- Êó•ËÆ∞Êìç‰ΩúÊ†è -->
			<view class="action-bar">
				<view class="filter-tabs">
					<view 
						class="tab-item" 
						:class="{ active: currentStatus === 'all' }"
						@tap="filterByStatus('all')"
					>
						ÂÖ®ÈÉ®
					</view>
					<view 
						class="tab-item" 
						:class="{ active: currentStatus === 'published' }"
						@tap="filterByStatus('published')"
					>
						Â∑≤ÂèëÂ∏É
					</view>
					<view 
						class="tab-item" 
						:class="{ active: currentStatus === 'draft' }"
						@tap="filterByStatus('draft')"
					>
