					</view>
				</view>
				
				<button class="add-btn" @tap="createDiary">
					<text class="add-icon">+</text>
					<text class="add-text">ÂÜôÊó•ËÆ∞</text>
				</button>
			</view>
			
			<!-- Êó•ËÆ∞ÂàóË°® -->
			<view class="diary-list">
				<view v-if="loading" class="loading-state">
					<text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
				</view>
				
				<view 
					v-else-if="filteredDiaries.length > 0"
					v-for="diary in filteredDiaries" 
					:key="diary.diaryId"
					class="diary-item"
					@tap="viewDiary(diary.diaryId)"
				>
					<view class="diary-header">
						<view class="diary-meta">
							<text class="diary-date">{{ formatDate(diary.createTime) }}</text>
							<view class="diary-status" :class="diary.status">
								{{ diary.status === 'published' ? 'Â∑≤ÂèëÂ∏É' : 'ËçâÁ®ø' }}
							</view>
						</view>
						<view class="diary-actions">
							<button class="action-btn edit" @tap.stop="editDiary(diary.diaryId)">ÁºñËæë</button>
							<button class="action-btn delete" @tap.stop="deleteDiary(diary.diaryId)">Âà†Èô§</button>
						</view>
					</view>
					
