						</view>
						<view v-if="diary.media && diary.media.length > 0" class="media-count">
							<text class="media-icon">üìé</text>
							<text class="media-text">{{ diary.media.length }}‰∏™ÈôÑ‰ª∂</text>
						</view>
					</view>
				</view>
				
				<view v-else class="empty-state">
					<text class="empty-icon">üìù</text>
					<text class="empty-text">ËøòÊ≤°ÊúâÊó•ËÆ∞ÔºåÂºÄÂßãËÆ∞ÂΩï‰Ω†ÁöÑÂøÉÊÉÖÂêß</text>
					<button class="empty-btn" @tap="createDiary">ÂÜôÁ¨¨‰∏ÄÁØáÊó•ËÆ∞</button>
				</view>
