</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { diaryApi } from '../../utils/api.js'
import { formatDateShort } from '../../utils/dateUtils.js'

const diaries = ref([])
const currentStatus = ref('all')
const loading = ref(false)

const filteredDiaries = computed(() => {
	if (currentStatus.value === 'all') {
		return diaries.value
	}
	return diaries.value.filter(diary => diary.status === currentStatus.value)
})

onMounted(() => {
	loadDiaries()
})

const loadDiaries = async () => {
	try {
		loading.value = true
		const userInfo = uni.getStorageSync('userInfo')
		const uid = userInfo && (userInfo.uid || userInfo.id || userInfo.userId)
		if (!uid) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			})
			return
		}
		
		// Ê†πÊçÆÂΩìÂâçÁä∂ÊÄÅÂÜ≥ÂÆöÊü•ËØ¢ÂèÇÊï∞
		// ËØ¥ÊòéÔºöÂêéÁ´ØÈÄöËøáËØ∑Ê±ÇÂ§¥ uid ËØÜÂà´Áî®Êà∑Ôºõ
		// ‰∏∫ÈÅøÂÖçËØØÂèñÂÖ®Á´ôÂÖ¨ÂºÄÔºåÈªòËÆ§Êü•ËØ¢ÂΩìÂâçÁî®Êà∑ÂÖ®ÈÉ®ÊàñÊåâÁä∂ÊÄÅÁ≠õÈÄâ„ÄÇ
		let queryParams = {}
		if (currentStatus.value !== 'all') {
			queryParams.status = currentStatus.value
		}
		
		// Ë∞ÉÁî®Êé•Âè£Ëé∑ÂèñÊó•ËÆ∞ÂàóË°®
		const response = await diaryApi.getDiaries(queryParams)
		console.log('Êó•ËÆ∞ÂàóË°®ÂìçÂ∫î:', response)
		if (response && response.code === 200) {
			diaries.value = response.data || []
			console.log('Êó•ËÆ∞Êï∞ÊçÆ:', diaries.value)
		} else {
			uni.showToast({
				title: response?.message || 'Ëé∑ÂèñÊó•ËÆ∞Â§±Ë¥•',
				icon: 'none'
			})
		}
	} catch (error) {
		console.error('Âä†ËΩΩÊó•ËÆ∞Â§±Ë¥•:', error)
		uni.showToast({
			title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
			icon: 'none'
		})
	} finally {
		loading.value = false
	}
}

const goToAnonymous = () => {
	uni.navigateTo({
		url: '/pages/anonymous/anonymous'
	})
}

const createDiary = () => {
	uni.navigateTo({
		url: '/pages/diary/edit'
	})
}

const viewDiary = (id) => {
	console.log('Êü•ÁúãÊó•ËÆ∞ÔºåID:', id)
	uni.navigateTo({
		url: `/pages/diary/detail?id=${id}`
	})
}

const editDiary = (id) => {
	console.log('ÁºñËæëÊó•ËÆ∞ÔºåID:', id)
	uni.navigateTo({
		url: `/pages/diary/edit?id=${id}`
	})
}

const deleteDiary = async (id) => {
	uni.showModal({
		title: 'Á°ÆËÆ§Âà†Èô§',
		content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÁØáÊó•ËÆ∞ÂêóÔºüÂà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§ç„ÄÇ',
		success: async (res) => {
			if (res.confirm) {
				try {
					const response = await diaryApi.deleteDiary(id)
					if (response && response.code === 200) {
						uni.showToast({
							title: 'Âà†Èô§ÊàêÂäü',
							icon: 'success'
						})
						// ÈáçÊñ∞Âä†ËΩΩÊó•ËÆ∞ÂàóË°®
						loadDiaries()
					} else {
						uni.showToast({
							title: response?.message || 'Âà†Èô§Â§±Ë¥•',
							icon: 'none'
						})
					}
				} catch (error) {
					console.error('Âà†Èô§Êó•ËÆ∞Â§±Ë¥•:', error)
					uni.showToast({
						title: 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
						icon: 'none'
					})
				}
			}
		}
	})
}

const filterByStatus = (status) => {
	currentStatus.value = status
	// ÂàáÊç¢Áä∂ÊÄÅÂêéÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
	loadDiaries()
}

const formatDate = (dateString) => {
	return formatDateShort(dateString)
}
